.service__block__price { padding: 40px 0 20px; border-bottom: 1px solid #fdfdfd; }

.service__block__price:nth-child(2) { padding-top: 0; }

.service__block__price:last-child { border-bottom: none; }

.service__block__price h4 { font-size: 14px; font-weight: 500; line-height: 1.29; letter-spacing: 1.4px; color: #131313; margin-bottom: 8px; }

.service__block__price strong { font-size: 18px; font-weight: bold; line-height: 1; letter-spacing: 1.8px; color: #131313; }

.service__block__price strong span { font-size: 14px; font-weight: normal; letter-spacing: 1.4px; }

.service__block__price p { font-size: 14px; font-weight: normal; line-height: 1.29; letter-spacing: 1.4px; color: #131313; margin-bottom: 5px; }

@media all and (-ms-high-contrast: none) { .media__content { display: flex; flex-wrap: wrap; justify-content: space-between; }
  .media__block { margin-bottom: 30px; }
  *::-ms-backdrop, .media__content { display: flex; flex-wrap: wrap; justify-content: space-between; }
  .media__block { margin-bottom: 30px; }
  .intro .container { margin: 0; } }
