.media {
  padding: 80px 0;
  background-image: linear-gradient(135deg, #ffffff, #f8f8f8);
  @include r(767) {
    padding: 30px 0; }
  &__content {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(365px, auto));
    grid-gap: 30px;
    grid-auto-rows: minmax(367px, auto);
    grid-auto-flow: dense;
    @include r(767) {
      grid-template-columns: repeat(auto-fill, minmax(200px, auto));
      grid-gap: 15px;
      grid-auto-rows: minmax(205px, auto); }
    @include r(470) {
      grid-template-columns: repeat(auto-fill, minmax(105px, auto)) !important;
      grid-gap: 15px !important;
      grid-auto-rows: minmax(105px, auto) !important; } }
  &__block {
    overflow: hidden;
    @include center;
    img {
      max-width: 100%;
      @include object-fit(cover);
      width: 100%;
      height: 100%;
      @include tr; }
    &-video {
      position: relative;
      a {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        &:hover {
          transform: translate(-50%, -50%) scale(1.1); }
        svg {
          width: 133px;
          @include r(767) {
            width: 78px; } } } } }
  .span-1 {
    grid-column-end: span 2;
    grid-row-end: span 1; }
  .span-2 {
    grid-column-end: span 1;
    grid-row-end: span 2; }
  &__btn {
    @include center;
    margin-top: 75px;
    @include r(767) {
      margin-top: 30px; }
    a {
      text-transform: uppercase;
      @include r(767) {
        padding: 16px 24px; }
      @include r(470) {
        padding: 12px 8px;
        font-size: 10px;
        i {
          margin-right: 5px; } }
      &:first-child {
        margin-right: 38px;
        @include r(767) {
          margin-right: 15px; }
        i {
          font-size: 14px; } } } } }
