.intro {
  background-image: url('../img/intro-bg.jpg');
  @include cover;
  height: 1080px;
  @include center;
  @include r(1040) {
    background-position: 25% 0; }
  @include r(767) {
    height: 850px;
    background-position: 25% 0;
    position: relative; }
  &__content {
    display: flex;
    align-items: flex-end;
    flex-direction: column;
    @include r(767) {
      align-items: center; } }
  &__logo {
    @include r(767) {
      width: 291px;
      background-color: #131313;
      padding: 17px;
      position: relative;
      top: 80px; }
    h1 {
      font-family: 'Ailerons';
      font-weight: normal;
      font-size: 89px;
      line-height: 1.2;
      text-align: right;
      color: #c2c8b8;
      text-transform: uppercase;
      @include r(767) {
        font-size: 77px;
        line-height: 1.1; } }
    p {
      font-size: 16px;
      font-weight: 500;
      line-height: 1.19;
      color: #c2c8b8;
      text-transform: uppercase;
      position: relative;
      display: flex;
      align-items: center;
      justify-content: flex-end;
      margin-top: 8px;
      @include r(767) {
        font-size: 14px;
        max-width: 195px;
        text-align: center;
        position: relative;
        left: 50%;
        transform: translate(-50%); }
      &:before, &:after {
        content: '';
        width: 95px;
        height: 1px;
        background: #dedede;
        display: block;
        @include r(767) {
          display: none; } }
      &:before {
        margin-right: 20px; }
      &:after {
        margin-left: 20px; } } }
  &__btn {
    position: relative;
    top: 220px;
    display: flex;
    align-items: center;
    @include r(767) {
      position: absolute;
      bottom: 0px;
      width: 100%;
      padding: 37px 0;
      background: rgba(#131313, 0.75);
      top: auto;
      flex-direction: column-reverse;
      align-items: center; }
    a:first-child {
      font-size: 12px;
      font-weight: normal;
      line-height: 1.25;
      letter-spacing: 1.2px;
      color: #fdfdfd;
      text-decoration: underline;
      margin-right: 40px;
      @include r(767) {
        margin-right: 0;
        margin-top: 15px; } } } }
